import { Formik } from "formik";
import CustomBtn from "../../CustomBtn";
import InputField from "../../InputField";
import { useDispatch } from "react-redux";
import { useEffect, useState } from "react";
import { HiOutlineEnvelope } from "react-icons/hi2";
import { Link, useNavigate } from "react-router-dom";
import { LOGIN_API_URL } from "../../../Utils/constant";
import { login } from "../../../Redux/features/Auth/authApi";
import { SIGNIN_INITIAL_VALUES } from "../../../Validations/InitialValues";
import { SIGNIN_VALIDATION_SCHEMA } from "../../../Validations/Validations";

const SignInForm = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const handleLoginSubmit = (values) => {
    const data = {
      apiEndpoint: LOGIN_API_URL,
      requestData: JSON.stringify(values),
    };

    dispatch(login(data)).then((res) => {
      if (res?.type === "login/fulfilled") {
        navigate("/admin/dashboard");
      }
    });
  };

  return (
    <div className=" flex align-center justify-center px-4 py-8 h-full m-3">
      <div className=" my-auto  md:w-96 w-full">
        <div className="my-8">
          {/* <img width={150} src={Images.LOGO_IMG} alt="" /> */}
          <h3 className="m-auto text-2xl text-blacks font-bold text-center w-max px-2 border-b border-gray-300 py-2 rounded-lg shadow-lg shadow-[#0051de49]">
            Babylon WorkShop
          </h3>
        </div>
        <h3 className="w-full text-lg font-bold mb-4">Log In</h3>
        <div className="w-full">
          <Formik
            initialValues={SIGNIN_INITIAL_VALUES}
            validationSchema={SIGNIN_VALIDATION_SCHEMA}
            onSubmit={(values) => {
              handleLoginSubmit(values);
            }}
          >
            {({
              values,
              errors,
              touched,
              handleBlur,
              handleChange,
              handleSubmit,
            }) => (
              <form onSubmit={handleSubmit} className="">
                <div className="">
                  <div className="mb-3">
                    <InputField
                      type="email"
                      name="email"
                      label={"Email "}
                      required={true}
                      value={values.email}
                      placeholder="Enter email"
                      onBlurHandle={handleBlur}
                      onChangeHandle={handleChange}
                      className="w-full mb-0 p-2 border rounded"
                      icon={<HiOutlineEnvelope className="text-gray-600" />}
                      error={errors.email && touched.email ? errors.email : ""}
                    />
                  </div>
                  <div className="mb-3">
                    <InputField
                      type="password"
                      name="password"
                      required={true}
                      label={"Password "}
                      value={values.password}
                      onBlurHandle={handleBlur}
                      placeholder="Enter password"
                      onChangeHandle={handleChange}
                      className="w-full mb-0 p-2 border rounded"
                      error={
                        errors.password && touched.password
                          ? errors.password
                          : ""
                      }
                    />
                  </div>
                </div>
                <div className="flex items-center justify-between mb-6">
                  <Link to="/forgotPassword">
                    <p className="text-right mb-0 text-[#0052DE] text-sm">
                      Forgot password
                    </p>
                  </Link>
                </div>

                <div className="mt-3">
                  <CustomBtn
                    text="Sign In"
                    type={"submit"}
                    className="w-full mb-2 py-3 text-white"
                  />
                </div>
              </form>
            )}
          </Formik>
        </div>
      </div>
    </div>
  );
};

export default SignInForm;
